<template>
    <div class="head" :style="headStyle" id="header">
      <div class="back" v-if="isShowGoBack" @click="goBack">
        <span class="icon-back"></span>
      </div>
      <h1 class="title">{{$route.meta.title}}</h1>
    </div>
</template>


<script>
export default {
  name: "Head",
  data() {
    const { background, backgroundColor, color } = this.$route.meta;
    const { path } = this.$route;
    const paths = path.substr(1).split("/");
    return {
      headStyle: {
        background: background || null,
        backgroundColor: backgroundColor || null,
        color: color || null
      },
      isShowGoBack: paths.length >= 2 && paths[1] !== "index"
    };
  },
  watch: {
    $route() {
      this.headStyle = {};
      const { path } = this.$route;
      const paths = path.substr(1).split("/");
      this.isShowGoBack = paths.length >= 2 && paths[1] !== "index";
      const { background, backgroundColor, color } = this.$route.meta;
      this.$nextTick(() => {
        this.headStyle = {
          background: background || null,
          backgroundColor: backgroundColor || null,
          color: color || null
        };
      });
    }
  },
  methods: {
    handleBack() {
      if (this.push.isShow) {
        this.$router.replace(this.$route.query.fullPath || "/");
      } else {
        this.$router.back();
      }
    },
    goBack() {
      this.$router.back();
    },
    /**
     * 判断android还是IOS
     */
    clientType(adrCallback, iosCallback) {
      const u = navigator.userAgent;
      if (u.indexOf("Android") > -1 || u.indexOf("Adr") > -1) {
        if (adrCallback) {
          adrCallback();
        }
      } else if (iosCallback) {
        iosCallback();
      }
    }
  }
};
</script>

<style scoped>
.head {
  color: #fff;
  height: 1.2rem;
  line-height: 1.2rem;
  background: #d11135;
  position: relative;
  z-index: 100;
}
.head .title {
  text-align: center;
  font-size: 0.6rem;
  font-weight: normal;
}
.back {
  position: absolute;
  left: 0;
  top: 0;
  width: 1.3rem;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.icon-back {
  width: 0.31rem;
  height: 0.69rem;
  background: url("../../images/icon-arrow-right.png") no-repeat;
  background-size: 100%;
  transform: rotate(180deg);
}
</style>




