<template>
  <div class="content">
    <Head />
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive" />
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive" />
    <Navigation />
  </div>
</template>

<script>
import Head from "~/header/head";
import Navigation from "~/navigation/index";

export default {
  name: "Entrance",
  components: {
    Head,
    Navigation
  }
};
</script>

<style scoped>

</style>



